<template>
    <div v-if="!loading">

        <b-card-group deck>
            <draggable class="w-100" en ghost-class="ghost" v-model="quizfrage.antworten">
                <!--                <transition-group name="flip-list" type="transition">-->
                <b-card :key="element.antwort" class="m-2" v-for="element in quizfrage.antworten">
                    {{element.antwort}}
                </b-card>
                <!--                </transition-group>-->
            </draggable>
        </b-card-group>

        <b-button @click="sendData" class="w-100" variant="primary">Senden</b-button>
    </div>
    <div class="text-center" v-else>
        <b-spinner class="mt-5"></b-spinner>
    </div>
</template>

<script>
    import draggable from "vuedraggable"
    import axios from "axios"

    export default {
        name: "Sorting",
        props: [],
        components: {
            draggable
        },
        data() {
            return {
                quizfrage: {},
                loading: true
            }
        },
        methods: {
            shuffle(a) {
                a.sort(() => Math.random() - 0.5);
                return a;
            },
            sendData() {
                let data = [];
                for (const antwort of this.quizfrage.antworten) {
                    data.push(antwort["antwort"])
                }
                const axiosOptions = {
                    method: 'POST',
                    headers: {'Content-Type': 'application/json'},
                    data: data,
                    url: this.$route.params.frage.replace(/§/g, "/") + "/validate"
                };
                // console.log(axiosOptions.data);
                axios(axiosOptions).then(res => console.log(res));
            }
        },
        mounted() {
            const url = this.$route.params.frage.replace(/§/g, "/");
            console.log(url);
            axios.get(url, {
                headers: {
                    Accept: "application/json"
                }
            }).then(res => {
                this.quizfrage = res.data;
                this.loading = false;
                this.quizfrage.antworten = this.shuffle(this.quizfrage.antworten);
            });
        }

    }
</script>

<style scoped>
    .ghost {
        opacity: 0.5;
        background: #c8ebfb;
    }

    .flip-list-move {
        transition: transform 0.5s;
    }
</style>